
@use "@styles/variables" as *

.project-item__link
	display: flex
	flex-direction: column
	gap: 8px
	font-weight: 500
	font-size: 18px
	line-height: 27px
	color: $black-75
	transition: all .2s ease-in-out

	&:hover
		color: $blue

		.project-item__image
			transform: scale(1.01)

.project-item__image
	transition: all .2s ease-in-out
